<template>
    <div class="book-grounding">
        <form id="bookInforForm" enctype="multipart/form-data" method="post" action="/dj_library/admin/addBook" @keyup.enter="submit" v-if="false">
            <div class="book-name div-common-margin">
                <label for="name-input">书名：</label><input name="bookName" type="text" id="name-input" class="input-common-style" v-model="bookName" v-focus>
            </div>
            <div class="book-price div-common-margin">
                <label for="price-input">价格：</label>
                <input type="text" name="price" id="price-input" v-model="bookPrice" class="input-common-style"> 元

                <label class="r-lable" for="author-input">作者：</label>
                <input type="text" name="bookAuthor" id="author-input" class="input-common-style" v-model="bookAuthor"> 
            </div>
            <div class="book-day div-common-margin">
                <label for="day-input">可借期限：</label>
                <input type="text" name="datecount" id="day-input" v-model="bookCanDay" class="input-common-style"> 天

                <label class="r-lable" for="category-select">书籍类别：</label>
                <!-- <input type="text" name="bookCategory" id="category-select" class="input-common-style" v-model="bookCatelogy">  -->
                <select name="bookCategory" id="category-select"  v-model="bookCategory">
                    <option v-for="(item,key) in bookCategoryItems" :key="key" :value="item.bookCategoryId" >
                        {{item.bookCategoryName}}
                    </option>
                </select>
                
                <label class="r-lable" for="publish-input">出版社：</label>
                <input type="text" name="bookPublish" id="publish-input" class="input-common-style" v-model="bookPublish"> 
            </div>
            <div class="book-description div-common-margin">
                <span>书籍简介：</span>
                <textarea name="desc" cols="80" rows="10" v-model="bookDescription"></textarea>
                <!-- <input type="text" id="decr-input" v-model="bookDescription" class="input-common-style" >  -->
            </div>
            <div class="book-pic div-common-margin">
                <span>书籍封面</span>
                <div class="pic-box">
                    <img id="pic" :src="bookPic" alt="">
                </div>
                <!-- <form id="uploadForm" enctype="multipart/form-data" method="post" action="XXXXXX"> -->
                <div id="uploadForm">
                    <input type="file" name="file" id="imageFile" onchange="fileSelected()" />
                </div>
                    
                <!-- </form> -->
            </div>
            <div class="save" id="bkgrd-submit" @click="clear">清空</div>
            <div class="save" id="bkgrd-submit" @click="submit">保存</div>
            
        </form>
        <div class="grounding-input">
            <label for="account">账号：</label><input type="text" id="account" v-focus v-model.trim="bookName" >
        </div>
    </div>
</template>
<script>
import VueEvent from '../../model/vueEvent'
export default {
    name:'book-grounding',
    data(){
        return {
            bookPrice:'',
            bookCanDay:'',
            bookName:'',
            bookDescription:'',
            bookAuthor:'',
            bookPublish:'',
            bookCategory:1,
            bookCategoryItems:'',
            bookPic:'',
        }
    },
    methods:{
        submit(){
            let bookInforForm = document.getElementById('bookInforForm');
            if(!this.inforLenthControl(this.bookName,2,10,'书名小于2位！','书名大于10位！','书名')){
                return false;
            }
            if(!this.isNumber(this.bookPrice,'价格')){
                return false;
            }
            if(!this.inforLenthControl(this.bookAuthor,2,10,'作者名称小于2位！','作者名称大于10位！','作者')){
                return false
            }
            if(!this.isNumber(this.bookCanDay,'可借期限')){
                return false;
            }
            if(!this.inforLenthControl(this.bookPublish,2,20,'出版社名称小于2位！','出版社名称大于20位！','出版社')){
                return false
            }
            if(!this.inforLenthControl(this.bookDescription,0,50,'书籍简介小于0位！','书籍简介大于50位！','书籍简介')){
                return false
            }
            if(!this.bookPic){
                alert('图片不能为空！')
                return false;
            }
            
            let that = this;
            $("#bookInforForm").ajaxSubmit(function (res) {
                console.log(res);
                if(res.code == 200){
                    alert('上架成功！')
                    that.$router.push('/library/new');
                }else{
                    alert('上架失败！');
                }
            });
        },
        inforLenthControl(item,minLen,maxLen,alertMin,alertMax,itemName){
            if(!item){
                alert(itemName+'不能为空！');
                return false;
            }
            if(item.length > maxLen){
                alert(alertMax)
                return false;
            }else if(item.length < minLen){
                alert(alertMin)
                return false;
            }
            return true;
        },
        isNumber(val,name){
            if(isNaN(val)){
                alert(name+'内请输入数字');
                return false;
            }
            return true;
        },
        getModifyInfor(){
            let that = this;
            VueEvent.$on('modify-book',data => {
                that.bookAuthor = data.bookAuthor;
                that.bookCategory = data.bookCategory;
                that.bookPrice = data.bookPrice;
                that.bookPublish = data.bookPublish;
                that.bookDescription = data.bookIntroduction;
                that.bookCanDay = data.dateCount;
                that.bookName = data.bookName;
                that.bookPic = data.bookImage;

                console.log(that.bookPrice,3)
                // for(let ele in data){
                //     if(this.ele || this.ele == ''){
                //         this.ele = data.ele;
                //     }
                // }
                console.log(data);
            })
        },
        clear(){
            this.bookPrice = '';
            this.bookCanDay = '';
            this.bookName = '';
            this.bookDescription = '';
            this.bookAuthor = '';
            this.bookPublish = '';
            // this.bookCategory:1,
            // this.bookCategoryItems:'',
            this.bookPic = '';
        }
    },
    created(){
        let that = this;
        VueEvent.$on('modify-book',data => {
            that.bookAuthor = data.bookAuthor;
            that.bookCategory = data.bookCategory;
            that.bookPrice = data.bookPrice;
            that.bookPublish = data.bookPublish;
            that.bookDescription = data.bookIntroduction;
            that.bookCanDay = data.dateCount;
            that.bookName = data.bookName;
            that.bookPic = data.bookImage;

            console.log(that.bookPrice,3)
            // for(let ele in data){
            //     if(this.ele || this.ele == ''){
            //         this.ele = data.ele;
            //     }
            // }
            console.log(data);
        })
        this.$http.post('/dj_library/admin/returnBookCategoryName')
            .then(res => {
                console.log(res);
                if(res.body.code == 200){
                    that.bookCategoryItems = res.body.data;
                }
            })
    },
    mounted(){
        
        // 图片上传
        let that = this;
        let iMaxFilesize = 2097152; //2M
        window.fileSelected = function() {
            let oFile = document.getElementById('imageFile').files[0];    //读取文件
            let rFilter = /^(image\/bmp|image\/gif|image\/jpeg|image\/png|image\/tiff)$/i;
            if (!rFilter.test(oFile.type)) {
                alert("文件格式必须为图片");
                return;
            }
            if (oFile.size > iMaxFilesize) {
                alert("图片大小不能超过2M");
                return;
            }
            // 获取 window 的 URL 工具
            let URL = window.URL || window.webkitURL;
            that.bookPic = URL.createObjectURL(oFile);
            // 通过 file 生成目标 url
            // let imgURL = URL.createObjectURL(oFile);
            // let picBox = document.getElementById('pic');
            // picBox.setAttribute('src',imgURL);

            
            // let vFD = new FormData(document.getElementById('uploadForm')),    //建立请求和数据
            //     oXHR = new XMLHttpRequest();
            // oXHR.addEventListener('load', function(resUpload) {
            //     //成功
            // }, false);
            // oXHR.addEventListener('error', function() {
            //     //失败
            // }, false);
            // oXHR.addEventListener('abort', function() {
            //     //上传中断
            // }, false);
            // oXHR.open('POST', actionUrl);
            // oXHR.send(vFD);
        };
        
    }
}
</script>
<style lang="less" scoped>
    .book-grounding {
        padding:50px 80px;
        font-size: 20px;
        .book-name {
            input {
                width: 371px;
                margin-left: 43px;
            }
        }
        .book-price {
            #price-input {
                width: 40px;
                margin-left: 39px;
            }
            #author-input {
                width: 100px;
                margin-left: 40px;
            }
        }
        .book-day {
            #day-input {
                width: 40px;
            }
            #category-select {
                width: 100px;
                outline: none;
                height: 35px;
                padding-left: 8px;
                font-size: 16px;
                cursor: pointer;
            }
            #publish-input{
                width: 130px;
            }
        }
        .book-description {
            textarea {
                vertical-align: text-top;
                outline: none;
                padding: 8px;
                font-size: 16px;
            }
        }
        .book-pic {
            .pic-box {
                // float: left;
                width: 180px;
                height: 180px;
                margin: -22px 0 0 107px;
                border: 1px solid #000;
                #pic {
                    width: 100%;
                    height: 100%;
                }
                
            }
            #uploadForm {
                margin: 5px 0 0 107px;
            }
        }
        .save {
            margin: 35px auto 0;
            width: 80px;
            height: 36px;
            line-height: 36px;
            text-align: center;
            background-color: #354457;
            color: #eee;
            border-radius: 8px;
            cursor: pointer;
        }
        .div-common-margin {
            padding: 20px;
            box-sizing: border-box;
        }
        .input-common-style {
            line-height: 30px;
            padding-left: 10px;
            font-size: 16px;
            // width: 230px;
            outline: none;
        }
        .r-lable {
            margin-left: 80px;
            &>input {
                width: 50px;
            }
        }
        .grounding-input {
            width: 300px;
            background-color: #E4EAF3;
            border-radius: 4px;
            padding-left: 8px;
            height: 32px;
            line-height: 32px;
            font-size: 16px;
            input {
                // padding-left: 8px;
                border: none;
                height: 28px;
                padding: 2px 8px;
                line-height: 20px;
                font-size: 14px;
            }
        }
    }
</style>





